import React, { Component } from 'react';
import Link from 'next/link';

class ProductList extends Component {
	constructor(props) {
		super(props);
		this.state = {}
	}
	render() {
		const {product} = this.props;
		const rawJson = product.short_description;
		const productDescription = rawJson.replace(/(<([^>]+)>)/ig,"");
		return (
			<div>
				<Link
					href={`/product`}
					as={`/product/${product.slug}`}
				>
					<img
						src={product.images[0].src}
						alt="Product image"
					/>
				</Link>
				<div className="product-headings">
					<h3>{product.name}</h3>
					<h5>${product.price}</h5>
					<h5 className="product-description">{productDescription}</h5>
				</div>
				<style jsx>{`
					img {
						max-width: 450px;
						background: url('https://www.google.com/url?sa=i&rct=j&q=&esrc=s&source=images&cd=&ved=2ahUKEwjr-PSZrN3jAhUUKH0KHThECGYQjRx6BAgBEAU&url=https%3A%2F%2Ftenor.com%2Fview%2Ffamily-guy-css-open-window-annoyed-pissed-gif-12014506&psig=AOvVaw1x0nNPcWlEv3bR8ciR-uje&ust=1564600230490230');
					}
					.product-headings {
						text-align: center;
					}
					.product-headings {
						text-transform: uppercase;
					}
				`}</style>
			</div>
		);
	}
}

export default ProductList;